<template>
    <div class="container">
        <div v-if="!user.auth" class="row d-flex justify-content-center py-3 my-5">
            <div class="col-12 text-center">
                <h1 class="theme-title mb-3">Авторизация на сайте β</h1>
                <h5 class="my-0">Должен быть активный аккаунт на <a class="theme-link" href="//iny.su/auth" target="_blank">iny.su</a></h5>
            </div>
            <div class="col-12 col-md-6 form-outline mb-3 mx-3">
                <input placeholder="username or login" type="text" class="form-control theme-text" @keypress.enter="auth()" v-model="login" />
                <label class="form-label theme-text active">username or login</label>
            </div>
             <div class="col-12 col-md-6 form-outline mt-3 mx-3">
                <input type="password" class="form-control theme-text" @keypress.enter="auth()" v-model="password" />
                <label class="form-label theme-text">password</label>
            </div>
            <div class="col-12 my-3 text-center">
                <h5>{{msg}}</h5>
            </div>
        </div>
        <div v-else class="row d-flex justify-content-center">
            <div class="col-12 text-center my-5">
                <h1 class="theme-title mb-3">Привет, {{user.name}} {{user.surname}}</h1>
                <h5 @click="this.$store.commit('LOGOUT', user.uid); user.auth = false">Выйти</h5>
            </div>
        </div>
    </div>
</template>

<script>
//const axios = require('axios')
export default {
    name: 'Auth',
    components: {

    },
    data: () => {
        return {
            login: '',
            password: '',
            msg: '',
            user: {
                auth: false
            }
        }
    },
    methods: {
        auth() {
            if (this.login && this.password != '') {
                this.$store.dispatch('LOGIN', {login: this.login, password: this.password})
                this.msg = 'Загрузка'
                setTimeout(() => {
                    if (this.$store.getters.IS_AUTH) {
                        this.user = this.$store.getters.USER
                    } else this.msg = 'неверные данные'
                }, 1000);
                
            } else this.msg = 'Заполните поля'
        }
    },
    mounted() {
        document.title = 'Auth'
        
        this.msg = 'Авторизуйтесь'

        if (this.$store.getters.IS_AUTH) {
            this.user = this.$store.getters.USER
        }
    },
    watch: {
        $store(to, from) {
            console.log(to, from)
        }
    }
}
</script>